<template>
    <div class="box">
        <div class="box-header with-border">
            <h3 class="box-title">Sửa banner</h3>
        </div>
        <div class="box-body">
            <form @submit.prevent="handleSubmit">
                <div class="box-body">
                    <div class="row">
                        <div class="col-lg-12">
                            <div class="form-group">
                                <label>Tên banner</label>
                                <input v-model="post.name" name="name" v-validate="'required|min:3|max:255'" type="text" :class="{'form-control':true, 'error-boder': errors.first('name')}" placeholder="Nhập tên danh mục...">
                                <span class="error">{{ errors.first("name") }}</span>
                            </div>
                            <div class="form-group">
                                <label>Link chuyển đến</label>
                                <input v-model="post.url" name="url" v-validate="'max:255'" type="text" :class="{'form-control':true, 'error-boder': errors.first('title')}" placeholder="Nhập title...">
                                <span class="error">{{ errors.first("url") }}</span>
                            </div>
                            <div class="form-group">
                                <label>Hình banner</label>
                                <input v-validate="'image'" data-vv-as="image" type="file" name="icon" v-on:change="onInputChange">
                                <span class="error">{{ errors.first("icon") }}</span>
                                <div id="upload-banner">
                                    <img v-if="imgPreview" class="banner-preview" v-bind:src="imgPreview">
                                </div>
                            </div>
                        </div>
                    </div>
                    <button type="submit" class="btn btn-primary" >Sửa</button>
                </div>
                <!-- /.box-body -->
            </form>
        </div>
        <!-- /.box-body -->
    </div>
</template>

<script>
import axios from "axios";
import { Validator } from 'vee-validate';

export default {
    metaInfo: {
        title: "Sửa banner"
    },
    data(){
        return {
            api: this.$store.state.domainApi,
            post:{
                name: "",
                url: "",
                image:""
            },
            file:{},
            imgPreview:false,
            dictionary:{
                vi: {
                    custom: {
                        // Tủy chỉnh theo từng trường
                        'name' : {
                            required: 'Vui lòng nhập tên banner !',
                            min: 'Tên danh mục tối thiểu 3 ký tự !',
                            max: 'Tên danh mục không được vượt quá 255 ký tự !'
                        },
                        "image":{
                            required: "Vui lòng chọn hình banner !",
                            image: "Vui lòng chọn định dạng hình ảnh !"
                        },
                        "url":{
                            max: 'Title không được vượt quá 255 ký tự !'
                        }
                    }
                }
            }
        }
    },
    methods: {
        handleSubmit() {
            this.$validator.validate().then(valid => {
                if (valid) {
                    const formData = new FormData();
                    
                    formData.set("id", this.$route.params.id);
                    formData.append("name", this.post.name);
                    formData.append("url", this.post.url);
                    formData.append("image", this.post.image);
                    formData.append("image_new", this.file);

                    axios.patch(`${this.api}/admin/banner`, formData, {
                        headers:{
                            Authorization: `Bearer ${localStorage.token}`
                        }
                    })
                    .then(response => {
                        if(response.data.success===1){
                            this.flash(response.data.message, 'success', {
                                timeout: 3000
                            });
                            this.$router.push({name: "route.admin.banner.index"});
                        }else{
                            this.flash(response.data.message, 'error', {
                                timeout: 3000
                            });
                        }
                        
                    });
                    
                }
            });
        },
        onInputChange(e){
            this.file = e.target.files[0];
            //phan laod anh local
            const reader = new FileReader();
            reader.onload = (e) => this.imgPreview = e.target.result;
            reader.readAsDataURL(this.file);
        }
    },
    created(){
        Validator.localize(this.dictionary);
        const validator = new Validator({ first_name: 'alpha' });
        validator.localize('vi');

        axios.get(this.api + "/admin/banner/"+this.$route.params.id,{
            headers:{
                Authorization: `Bearer ${localStorage.token}`
            }
        })
        .then(response=>{
            this.post.name = response.data.name;
            this.post.url = response.data.url;
            this.post.image = response.data.image;
            this.imgPreview = this.api + "/image/banner/"+ response.data.image;
        });
    }
}
</script>
<style>
.form-group{
    margin-bottom: 0px;
}
.form-group label{
    display: block;
}
.error{
    font-weight: 600;
    color: red;
    margin-left: 12px;
    margin-top: 3px;
    margin-bottom: 10px;
    display: inline-block;
}
.error-boder{
    border-color: red !important;
}
#upload-banner{
    background: #009688;
    display: block;
    outline: 2px dashed white;
    outline-offset: -10px;
    padding: 20px;
    color: white;
    position: relative;
    text-align: center;
}
.banner-preview{
    height: 100%;
    width: 100%;
    background-size:contain;
    background-repeat: no-repeat;
    background-position: center;
}
</style>